
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'app',
components: {
XHeader
},
computed: {
title: function () {
if (this.$route.path === '/') return 'e'
if (this.$route.path === '/flowdetail/1' || this.$route.path === '/docsign') return ''
if (this.$route.path === '/flowdetail/2' || this.$route.path === '/flowdetail/3' || this.$route.path === '/flowdetail/4') return ''
if (this.$route.path === '/docview/1' || this.$route.path === '/docview/2' || this.$route.path === '/docview/3') return ''
if (this.$route.path === '/myfiles') return ''
if (this.$route.path === '/userlist/1' || this.$route.path === '/userlist/2' || this.$route.path === '/userlist/3') return ''
return 'e'
},
preventGoBack: function () {
if (this.$route.path === '/flowdetail/4' || this.$route.path === '/') {
return true
}
}
},
methods: {
onClickBack () {
this.$router.push({ path: '/' })
}
}
}



